
*{
  font-family:Helvetica, Arial, sans-serif
}

body {
  padding: 30px;
  
}


input {
  color: green;
  font-size: 1.2rem;
  width: 80%;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-top: 10px;
  overflow: hidden;
  
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}

.card-grid{
  display:flex;
  flex-direction: row;
  flex-wrap:wrap;
}
